.page-inner__wrapper {
  padding: 94px 20px 45px;
  background-color: #283645;
  background-image: url("../img/backgrounds/bg-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  @media @2dppx {
    background-image: url("../img/backgrounds/bg-mobile@2x.jpg");
  }

  @media @tablet-width {
    padding: 178px 20px 58px;
    background-image: url("../img/backgrounds/bg-tablet.jpg");

    @media @2dppx {
      background-image: url("../img/backgrounds/bg-tablet@2x.jpg");
    }
  }

  @media @desktop-width {
    padding: 170px 0 87px;
    background-image: url("../img/backgrounds/bg-desktop.jpg");

    @media @2dppx {
      background-image: url("../img/backgrounds/bg-desktop@2x.jpg");
    }
  }

  &--catalog {
    padding-bottom: 76px;

    @media @tablet-width {
      padding-bottom: 127px;
    }

    @media @desktop-width {
      padding-bottom: 88px;
    }
  }
}

.page-inner__title {
  .no_indent();
  color: #ffffff;
  text-align: center;
  font-weight: 300;
  font-size: 24px;
  line-height: 33px;

  @media @tablet-width {
    font-size: 50px;
    line-height: 68px;
  }

  @media @desktop-width {
    width: 940px;
    margin: 0 auto;
    padding: 0 80px;
  }
}
